.container {
   
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    margin: 0 auto;

    @media screen and(min-width: 320px) {
        width: 320px;
        
    }

    @media screen and(min-width: 768px) {
        width: 768px;
    }

    @media screen and(min-width: 1280px) {
        width: 1280px;
    }
}